import { Artist, IArtistName, IArtistSearch } from "../backend/Artist/artist";
import { IGetManyData, IGetManyParams } from "../backend/Common/getManyData";
import { LangCode } from "../backend/Common/language";
import { OnlyId } from "../backend/Common/onlyId";
import { IPutParams } from "../backend/Common/putParams";
import { ObservableApiFunctionWithArgs, ObservableApiFunctionWithArgsDirect, ObservableApiFunctionWithoutArgsDirect, ToService } from "../helpers/observables";

export interface IArtistService{

    updateLastest:ObservableApiFunctionWithoutArgsDirect<void>;
    search:ObservableApiFunctionWithArgsDirect<string,IArtistSearch[]>;

    getAll:ObservableApiFunctionWithArgs<IGetManyParams,IGetManyData<IArtistSearch>>;
    post:ObservableApiFunctionWithArgs<IArtistName,OnlyId>;
    put:ObservableApiFunctionWithArgs<IPutParams<IArtistName[]>,OnlyId>;
    delete:ObservableApiFunctionWithArgs<IPutParams<(keyof typeof LangCode)[]>,OnlyId>
}


export default ToService<IArtistService>(Artist);