import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';

@Component({
  selector: 'app-aspect-ratio-warning',
  templateUrl: 'aspect-ratio-warning.component.html',
  styleUrls: ['aspect-ratio-warning.component.scss'],
  standalone: true,
  imports: [CommonModule, TranslateModule],
})
export class AspectRatioWarningComponent implements OnChanges {
  @Input() aspectRatio!: number;
  isAspectRatioCorrect: boolean = true;

  ngOnChanges(changes: SimpleChanges) {
    if (changes['aspectRatio'] && !changes['aspectRatio'].firstChange) {
      this.isAspectRatioCorrect = this.aspectRatio >= 9 / 16 && this.aspectRatio <= 16 / 9;
    }
  }
}
